

var obj = {
	myInfor: {},
	openLogin: false,
	fn:{
		/*
		*
		+
		--obj :
		--target:
		+{"flag":flag, "index":index }
		--flag:true
		--index
		*/
		isExist: function (obj,target) {
			var flag = false;
			var index ;

			obj && obj.forEach(function (ele,i) {
				if (ele.id == target) {
					flag = true;
					index = i;
				}
			});
			return {"flag":flag, "index":index };
		}
	}
};



export default obj;










